.dialogsItems {
    overflow: auto;
    grid-area: a;
    border-radius: 5px;
    padding: 0 5px 15px 15px;
    box-shadow:  0 0 10px 3px rgba(0, 0, 0, 0.62), inset 0 0 3px rgba(1, 255, 81, 0.25);
    height: 250px;
}

.dialog {
    margin-top: 15px;
}

.dialog img{
    width: 25px;
    margin-right: 15px;
    border-radius: 5px;
    vertical-align: middle;
}

.dialog a {
    color: azure;
    text-decoration: none;
}

.dialogs a.active {
    text-shadow: 2px 2px 4px rgba(10, 210, 49, 0.72);
    text-shadow: 2px 2px 4px rgb(135, 136, 140);
    padding: 5px 5px;
    border-radius: 3px;
}

.dialog a:hover {
    background-size: 130%;
    border-radius: 3px;
    padding: 7px 7px;
}

.dialog a:active {
    padding: 5px 5px;
}

.dialog a.active:hover {
    padding: 5px 5px;
}

.openChat{
    z-index: 11;
    position: fixed;
    right: 5px;
    bottom: 5px;
    width: 165px;
}

.openChat button{
    cursor: pointer;
    width: 80px;
    border: none;
    border-radius: 5px;
    background-color: rgba(1, 255, 81, 0.48);
    box-shadow:  0 0 5px rgba(1, 255, 81, 0.48);
}

.closeChat {
    position: absolute;
    bottom: -25px;
    width: 100%;
    text-align: center;
}

.closeChat button {
    cursor: pointer;
    display: inline-block;
    width: 80px;
    border: none;
    border-radius: 5px;
    background-color: rgba(255, 67, 67, 0.68);
    box-shadow:  0 0 5px 1px rgba(255, 67, 67, 0.68), inset 0 0 5px 2px rgba(0, 0, 0, 0.62);
}

.messages {
    z-index: 99;
    position: fixed;
    background-color: rgba(44, 43, 43, 0.98);
    border-radius: 5px;
    padding: 5px;
    margin-left: 10px;
    box-shadow:  0 0 10px 3px rgba(0, 0, 0, 0.62), inset 0 0 3px rgba(1, 255, 81, 0.25);
    grid-area: b;
    right: 5px;
    bottom: 30px;
    width: 250px;
}

.messageChat {
    border-radius: 5px;
    padding: 5px;
    height: 300px;
    overflow-y: scroll;
}

.messageChat::-webkit-scrollbar {
    width: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.62);
    border-radius: 5px;
}

.messageChat::-webkit-scrollbar-thumb {
    width: 5px;
    background-color: rgba(1, 255, 81, 0.4);
    border-radius: 5px;
}

.messageItem,  .messageItemAuth{
    display: flex;
    margin-bottom: 7px;
    box-shadow:  0 0 10px 3px rgba(0, 0, 0, 0.62), 0 0 3px rgba(1, 255, 81, 0.25);
    padding: 5px;
    width: 190px;
    border-radius: 5px;
}

.messageItemAuth {
    margin-left: auto;
    box-shadow:  0 0 10px 3px rgba(0, 0, 0, 0.62), inset 0 0 3px rgba(1, 255, 81, 0.25);
}

.photo {
    text-align: center;
    margin-right: 10px;
    font-size: 14px;
}

.photo img {
    margin-top: 5px;
    display: inline-block;
    width: 30px;
    border-radius: 5px;
}

.userName {
    font-size: 13px;
}

.message {
    font-size: 11px;
    width: 150px;
    min-height: 20px;
    border-radius: 5px;
    word-wrap: break-word;
}

.addMessage {
    width: 100%;
}

.addMessage button {
    margin-top: 10px;
    float: right;
    color: azure;
    border: none;
    background-color: rgba(9, 37, 94, 0.34);
    background-size: 130%;
    border-radius: 3px;
    padding: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.62);
}

.addMessage button:disabled {
    color: rgba(240, 255, 255, 0.1);
    background-color: rgba(2, 2, 2, 0.25);
}

.addMessage button:disabled:hover {
    color: rgba(240, 255, 255, 0.1);
    background-color: rgba(2, 2, 2, 0.25);
    margin-top: 10px;
    padding: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.62);
}

.addMessage button:hover {
    background-color: rgba(1, 255, 81, 0.25);
    margin-top: 6px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.62);
    padding: 7px 7px;
}

.addMessage button:active {
    margin-top: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.62);
    padding: 5px 5px;
}